import { Component, OnInit } from '@angular/core';
import {Router } from '@angular/router';
import {ApiService } from '../services/api/api.service';
import { GlobalFooService } from '../services/globalFooService.service';
import {CommonService} from '../services/common.service';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.page.html',
  styleUrls: ['./settings.page.scss'],
})
export class SettingsPage implements OnInit {

  userData : any;
  toggle: boolean = false;
  userId : any = localStorage.getItem('vwd_userid');

  constructor(
    public router:Router,
    private globalFooService: GlobalFooService,
    private common: CommonService,
    public api:ApiService,
  ) { }

  ionViewDidEnter(){
    this.userData   = JSON.parse(localStorage.getItem('vwd_userData'));
    this.toggle = this.userData.pushNotification == 1 ? true : false;
  }

  ngOnInit() {
   
  }

  UpdateNotification(status)
  {
      console.log(status);
      let type = status == true ? 0 : 1 ;
      console.log(type);
      let dict ={
        userId: this.userId,
        pushNotification: type,
      };
      this.api.post('updatePushNotificationStatus', dict,'')
      .subscribe(
        (result) => {
          this.common.stopLoading();
          let res: any;
          res = result;
          if(res.status == 422 )
          {
            var errMsgs = '';
            for (var i = 0; i < res.errors.length; i++){
              var obj = res.errors[i];
              errMsgs += obj +'</br>';
            }
            this.common.presentToast(errMsgs,'danger');
          }
          else if(res.status == 200)
          {

            if(type == 1)
            {
              this.toggle = false;
            }
            else{
              this.toggle = true;
            }

            localStorage.setItem('vwd_userData', JSON.stringify(res.data));
            this.common.presentToast('Updated Successfully!.','success');
          }
        },
        (error) => {
          console.log(error);
        });
  }

  logout(){
    //this.storage.clear();
    localStorage.clear();
    this.router.navigate(['/login']);
  }

}
